import React, { Component } from 'react';
import { Col, Row } from 'antd';
import styled from 'styled-components';
import {
  ContainerWrapper,
  Description,
  MarketButton,
  Section,
  SectionHeader
} from '@src/Styles';
import { size } from '@src/breakpoints';

interface Jobs {
  title: string;
  link: string;
}

interface Props {
  list: Jobs[];
}

const MarketSmallText = styled.p`
  font-weight: 100;
`;

const JobDescription = Description.extend`
  line-height: 45px;
`;

const JobMarketButton = MarketButton.extend`
  @media (max-width: ${size.tablet}) {
    && {
      min-width: 90px;
      font-size: 14px;
    }
  }
`;

class JobList extends Component<Props> {
  render() {
    return (
      <Section id="jobList" bg={'#f0f0f0'}>
        <ContainerWrapper>
          <SectionHeader color={'#000'}>Open Positions</SectionHeader>
          {this.props.list.map((job, index) => {
            return (
              <Row
                key={index}
                type="flex"
                justify="space-between"
                style={{ marginTop: '50px' }}
              >
                <Col xs={18}>
                  <JobDescription color={'#000'}>{job.title}</JobDescription>
                </Col>
                <Col xs={6} style={{ textAlign: 'right' }}>
                  <a href={job.link} target="_blank">
                    <JobMarketButton type="primary">View</JobMarketButton>
                  </a>
                </Col>
              </Row>
            );
          })}
          <MarketSmallText style={{ marginTop: '70px' }}>
            If you have any questions about working at Market Protocol, please
            email us at{' '}
            <a
              href="mailto:jobs@marketprotocol.io"
              style={{ textDecoration: 'none', color: '#008B77' }}
            >
              {' '}
              jobs@marketprotocol.io
            </a>
          </MarketSmallText>
        </ContainerWrapper>
      </Section>
    );
  }
}

export default JobList;



