import React from 'react';
import { Button, Col, Row } from 'antd';
import styled from 'styled-components';
import header from '@images/jobs/jobs-hero.svg';
import { device, size } from '@src/breakpoints';
import {
  ContainerWrapper,
  Description,
  HeroHeader,
  HeroText,
  MarketButton
} from '@src/Styles';
import { Link } from 'react-static';

export const HeroArt = styled.div`
  text-align: right;
  width: 100%;
  height: 100%;

  img {
    width: 100%;
    position: absolute;
    right: 0;
    bottom: 0;
  }

  @media ${device.tablet} {
    img {
      width: 125%;
    }
  }

  @media ${device.laptop} {
    img {
      width: 100%;
    }
  }
`;

export const TextWrapper = styled.div`
  padding: 100px 0;

  @media (max-width: ${size.tablet}) {
    padding: 25px 25px 50px 25px;
  }
`;

const JobSection = styled.section`
  background: #181e26;
`;

class Jobs extends React.Component {
  render() {
    return (
      <JobSection>
        <ContainerWrapper>
          <Row type="flex" className="hero">
            <Col xs={24} sm={24} md={14} lg={12} xl={12} style={{ zIndex: 1 }}>
              <Row type="flex" align="middle" style={{ height: '100%' }}>
                <TextWrapper>
                  <HeroHeader>Jobs</HeroHeader>
                  <Description color={'#fff'} style={{ margin: '25px 0' }}>
                    Headquartered in beautiful Boulder, Colorado <br /> with a
                    world-wide distributed team
                  </Description>
                  <Link
                    to="#jobList"
                    style={{ color: 'inherit', textDecoration: 'none' }}
                  >
                    <MarketButton type="primary" size={'large'} href={''}>
                      Browse Jobs
                    </MarketButton>
                  </Link>
                </TextWrapper>
              </Row>
            </Col>
            <Col xs={0} sm={0} md={10} lg={12} xl={12}>
              <HeroArt>
                <img alt="MARKET Protocol jobs" src={header} />
              </HeroArt>
            </Col>
          </Row>
        </ContainerWrapper>
      </JobSection>
    );
  }
}
export default Jobs;



